import {graphql, withPrefix} from 'gatsby';
import Img from 'gatsby-image';
import React from 'react';

import {Button} from '../../../components/button';
import {CaseHeader} from '../../../components/case_header';
import {WillAnimate} from '../../../components/content_animate';
import {Layout} from '../../../components/layout';
import {ScrollTo} from '../../../components/scroll_to';

const locale = 'en';
const translatedPagePath = '/nl/portfolio/waterrower-connect-apps/';

export default ({data}: any) => (
    <Layout
        title="Case: Apps for WaterRower – The ultimate home workout – Showcase of our work"
        description="For WaterRower we developed the WaterRower Connect apps to enrich their high-quality rowing machines with software."
        ogImage="waterrower.png"
        translatedPagePath={translatedPagePath}
        locale={locale}
        navColor="white">
        <CaseHeader
            urlBack="/en/portfolio/mobile-road-signaling-tti-tactic/"
            titleBack="Case: Mobile road signaling TTI TACTIC"
            urlForward="/en/portfolio/saas-product-keeping/"
            titleForward="Case: Our own product: Keeping"
            translatedPagePath={translatedPagePath}
            locale={locale}>
            <Img className="hero__image" alt="RP3" fluid={data.waterRowerMain.childImageSharp.fluid} critical />
            <div className="hero__content-wrapper">
                <div className="hero__content">
                    {/* Make sure to have the correct spacing when br becomes invisible */}
                    <h1>
                        The ultimate <br /> home workout
                    </h1>
                    <h2>Native apps for WaterRower.</h2>
                </div>
            </div>
            <ScrollTo className="hero__scroll" target="/nl/portfolio/waterrower-connect-apps/#from-hero" />
        </CaseHeader>

        <section className="section">
            {/* Case intro */}

            <WillAnimate as="article" id="from-hero" className="content">
                <div className="container">
                    <div className="content__display content__display--intro">
                        <h2>
                            WaterRower produces high-quality rowing machines for home use. To support the user of the{' '}
                            WaterRower in their activities, we developed the{' '}
                            <span className="highlight">WaterRower Connect</span> mobile apps for iOS and Android{' '}
                            together with WaterRower.
                        </h2>
                    </div>
                </div>
            </WillAnimate>
        </section>

        <section className="section">
            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        {/* Make sure to have the correct spacing when br becomes invisible */}
                        <h2>Carefree rowing</h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            WaterRower Connect provides the recreational rower with data on their rowing performance by{' '}
                            turning their mobile phone into a performance monitor that connects to their rowing machine.{' '}
                        </p>
                        <p>
                            You can connect the WaterRower Monitor to your smartphone's WaterRower Connect application{' '}
                            through Bluetooth. This turns your smartphone into a performance monitor for all your rowing{' '}
                            needs.
                        </p>
                        <p>
                            As a recreational rower you can design your own rowing experience: you decide how far, how{' '}
                            long and how hard you row. The WaterRower connect apps accurately keep track of your{' '}
                            performance for you. Several modes of training support you in choosing a proper training{' '}
                            style for your workout.
                        </p>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="image">
                    <div className="image__content">
                        <Img
                            alt="This is what the interface of WaterRower Connect looks like"
                            fluid={data.waterRowerAppInterface.childImageSharp.fluid}
                        />
                    </div>
                    <div className="image__caption">
                        <span className="ion-md-arrow-dropright">
                            The WaterRower Connect interface as displayed on an iPhone. Seamless, simple and elegant.
                        </span>
                    </div>
                </div>
            </WillAnimate>

            {/* Article apps */}

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content-group">
                        <div className="content-group__background content-group__background--right" />
                        <div className="content__image">
                            <div className="image">
                                <div className="image__content">
                                    <Img alt="alt title" fluid={data.waterRowerApp.childImageSharp.fluid} />
                                </div>
                            </div>
                        </div>
                        <div className="content__copy">
                            <h2>Android- and iOS-apps</h2>
                            <p>
                                The WaterRower Connect mobile apps are characterized through their user friendly design.{' '}
                                They were developed and designed with simplicity and ease of use in mind. Exactly what
                                you would expect from a home rowing app.
                            </p>
                            <p>
                                Through accurate measurement and information exchange with WaterRower Monitor, the apps
                                show your performance in the form of time, distance, calories, strokes (per minute and
                                total), speed and split (500m).
                            </p>
                            <p>
                                All your workouts will be saved and synced with your account so that you have all your{' '}
                                rowing data in a centralized place, available on any device, anywhere.
                            </p>
                        </div>
                    </div>
                </div>
            </WillAnimate>

            <WillAnimate as="article" className="content">
                <div className="container">
                    <div className="content__display">
                        {/* Make sure to have the correct spacing when br becomes invisible */}
                        <h2>
                            Looking to <br />
                            enrich your <br />
                            product?
                        </h2>
                    </div>
                    <div className="content__copy">
                        <p className="p-lead">
                            For WaterRower we developed the WaterRower Connect apps to enrich the home rower with well{' '}
                            though-out software. Do you have an interesting idea for your product? Contact{' '}
                            <span className="inline-avatar">
                                <Img alt="Joris" fixed={data.joris.childImageSharp.fixed} Tag="span" />
                                <span>Joris</span>
                            </span>{' '}
                            for an exploratory chat.
                        </p>
                    </div>
                    <div className="content__buttons content__buttons--50">
                        <Button
                            url="/en/contact/"
                            name="Contact us"
                            title="Contact information Enschede and Amsterdam"
                        />
                    </div>
                </div>
            </WillAnimate>
        </section>
    </Layout>
);

export const query = graphql`
    query {
        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }
        waterRowerMain: file(relativePath: {eq: "portfolio/waterrower/waterrower-hero.jpg"}) {
            ...fullscreenHero
        }
        waterRowerApp: file(relativePath: {eq: "portfolio/waterrower/app.jpg"}) {
            ...fluid800Width
        }
        waterRowerAppInterface: file(relativePath: {eq: "portfolio/waterrower/interface.png"}) {
            ...fluid1200Width
        }
    }
`;
